    viewTypeMode( viewType: string ) {
        if ( viewType === 'summary' ) {
            this.viewTypeSummary = true;
            this.viewTypeDetail = false;

        } else if ( viewType === 'detail' ) {
            this.viewTypeSummary = false;
            this.viewTypeDetail = true;

        } else {
            this.viewTypeSummary = false;
            this.viewTypeDetail = false;
        }
    }

